html {
  font-size: 16px;
  line-height: 1.15;
}
body {
  background: #fff;
  font-weight: 400;
  font-family: 'Montserrat', sans-serif;
  color: #000;
  overflow-wrap: break-word;
  word-wrap: break-word;
}
img {
  max-width: 100%;
}
.element .element__button,
.header .header__subscribe-link {
  display: block;
  color: #fff;
  background-color: #375573;
  font-size: 0.75rem;
  font-weight: 500;
  text-transform: uppercase;
  text-align: center;
  text-decoration: none;
}
.widget .widget__title {
  font-size: 1.125rem;
  font-weight: 700;
  line-height: 68px;
  text-align: center;
  border: 1px solid #dde2e7;
  border-radius: 5px;
  color: #1a1a1a;
}
.container {
  min-width: 320px;
  max-width: 100%;
  padding: 0 0;
  box-sizing: border-box;
  margin: 0 auto;
}
.wrapper {
  min-width: 320px;
  max-width: 1200px;
  padding: 0 15px;
  box-sizing: border-box;
  margin: 0 auto;
}
main.wrapper {
  overflow: hidden;
}
.icon .icon__link {
  display: inline-block;
  margin: 0 30px 0 0;
}
.icon .icon__link:last-child {
  margin-right: 0;
}
.icon .icon__img {
  display: block;
  width: 23px;
  height: 23px;
}
.pagination {
  display: block;
  overflow: hidden;
}
.pagination .pagination__first,
.pagination .pagination__last {
  font-size: 0.875rem;
  font-weight: 700;
  line-height: 1.2;
  color: #000;
  text-transform: uppercase;
  text-decoration: none;
  transition: color 0.2s;
}
.pagination .pagination__first:hover,
.pagination .pagination__last:hover {
  color: #e84c50;
}
.pagination .pagination__first {
  float: left;
}
.pagination .pagination__first::before {
  content: "";
  background: url("../img/angle-double-left-black.svg") center center no-repeat;
  display: inline-block;
  width: 20px;
  height: 15px;
  vertical-align: middle;
  margin: -3px 5px 0 0;
}
.pagination .pagination__last {
  float: right;
}
.pagination .pagination__last::after {
  content: "";
  background: url("../img/angle-double-right-black.svg") center center no-repeat;
  display: inline-block;
  width: 20px;
  height: 15px;
  vertical-align: middle;
  margin: -3px 0 0 5px;
}
.pagination .pagination__list {
  max-width: 170px;
  margin: 0 auto;
  text-align: center;
}
.pagination .pagination__list a {
  font-size: 0.875rem;
  font-weight: 700;
  line-height: 1.2;
  color: #000;
  text-transform: uppercase;
  text-decoration: none;
  transition: color 0.2s;
  margin: 0 7px;
}
.pagination .pagination__list a:hover {
  color: #e84c50;
}
.widget {
  margin: 0 0 40px 0;
}
.widget .widget__title {
  margin: 0 0 10px 0;
}
.widget .widget__element {
  padding: 30px 15px;
  border-bottom: 1px solid #dde2e7;
}
.widget .widget__element:last-child {
  border: none;
}
.widget .widget__element-img {
  width: 82px;
  height: 82px;
  margin: 0 auto 27px auto;
  border-radius: 50%;
  overflow: hidden;
}
.widget .widget__element-img img {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
}
.widget .widget__element-title {
  font-size: 1.125rem;
  font-weight: 400;
  font-family: 'Playfair Display', serif;
  color: #1a1a1a;
  line-height: 1.2;
  text-align: center;
  margin: 15px 0;
}
.widget .widget__element-date {
  font-size: 0.75rem;
  font-weight: 500;
  text-transform: uppercase;
  line-height: 1.2;
  text-align: center;
  color: #b0b0b0;
  margin: 15px 0 10px 0;
}
.widget .widget__box {
  margin: 40px 0 0 0;
  overflow: hidden;
}
.widget .widget__instagram-img {
  float: left;
  width: 44.44444444444444%;
  margin-bottom: 30px;
  border-radius: 3px;
  overflow: hidden;
  cursor: pointer;
}
.widget .widget__instagram-img:nth-child(odd) {
  margin-right: 30px;
}
.widget .widget__instagram-img img {
  display: block;
  width: 100%;
  height: 100px;
  object-fit: cover;
  transform: scale(1);
  transition: transform 6s;
}
.widget .widget__instagram-img img:hover {
  transform: scale(1.3);
}
.element {
  padding-bottom: 40px;
  border-bottom: 1px solid #dde2e7;
}
.element .element__title {
  font-size: 1.875rem;
  font-family: 'Playfair Display', serif;
  line-height: 1.2;
  color: #1a1a1a;
  text-align: center;
  margin: 0 0 25px 0;
}
.element .element__date {
  font-size: 0.875rem;
  font-weight: 700;
  line-height: 1.2;
  color: #b0b0b0;
  text-transform: uppercase;
  text-align: center;
  margin: 25px 0;
}
.element .element__img {
  margin: 35px 0;
}
.element .element__img img {
  display: block;
  width: 100%;
  object-fit: cover;
}
.element .element__text {
  color: #1a1a1a;
  line-height: 1.75;
  margin: 30px 0;
}
.element .element__footer {
  overflow: hidden;
}
.element .element__button {
  float: left;
  width: 160px;
  line-height: 42px;
  border-radius: 8px;
}
.element .element__button::after {
  content: "";
  background: url("../img/angle-double-right.svg") center center no-repeat;
  display: inline-block;
  width: 20px;
  height: 15px;
  vertical-align: middle;
  margin: -3px 0 0 5px;
}
.element .element__icon {
  float: right;
  margin-top: 9px;
}
.header {
  border-bottom: 1px solid #dde2e7;
  user-select: none;
}
.header .header__wrapper {
  max-width: 1600px;
  padding-top: 50px;
  padding-bottom: 50px;
  margin: 0 auto;
  overflow: hidden;
}
.header .header__icon {
  float: left;
  margin: 12px 0 0 60px;
}
.header .header__title {
  text-align: center;
  font-size: 2.375rem;
  font-weight: 900;
  font-family: 'Exo 2', sans-serif;
  color: #1a1a1a;
  margin: 0;
}
.header .header__box {
  float: right;
  overflow: hidden;
  margin: 4px 60px 0 0;
}
.header .header__search {
  float: left;
  margin: 3px 40px 0 0;
}
.header .header__search-link {
  display: block;
}
.header .header__search-img {
  display: block;
  width: 28px;
  height: 28px;
}
.header .header__subscribe-link {
  float: right;
  width: 150px;
  line-height: 36px;
  border-radius: 18px;
}
.content {
  float: left;
  width: calc(100% - 30px - 270px);
  margin: 55px 0 80px 0;
}
.sidebar {
  float: right;
  width: 270px;
  margin: 55px 0 80px 0;
}
.footer {
  background-color: #dde2e7;
}
.footer::after {
  content: "© Copyright 2019. All rights reserved.";
  font-size: 0.875rem;
  line-height: 1.2;
  display: block;
  background-color: #e8ebee;
  color: #b7babe;
  text-align: center;
  padding: 20px 0;
}
.footer .footer__wrapper {
  overflow: hidden;
}
.footer .footer__menu {
  float: left;
  list-style: none;
  padding: 0;
  margin: 55px 0 70px 0;
  columns: 175px 3;
  column-gap: 110px;
}
.footer .footer__menu-item {
  font-weight: 500;
  line-height: 1.2;
  margin: 0 0 15px 0;
}
.footer .footer__menu-link {
  color: #8e9399;
  text-decoration: none;
}
.footer .footer__icon {
  float: right;
  margin: 50px 140px 0 0;
}
.preview .preview__element {
  margin: 0 0 60px 0;
}
.preview .preview__element:last-of-type {
  margin-bottom: 35px;
}
